import React from 'react';
import { StyledCardList } from '../styles/Card';
import CardItem from './CardItem';

const CardList = ({ clothes, selectedCategory, loading, cat, currentPosts, activeLink, testCat }) => {

  const selectedCategoryDropown = clothes.filter(el => el.category === testCat);
  // console.log("selectedCategoryDropown", selectedCategoryDropown)

  const renderedList = currentPosts.map(cloth => {
    return (
      <CardItem cloth={cloth} />
    )
  });
  const renderedCategoryList = currentPosts.filter(el => el.category === testCat).map(cloth => {
    console.log("selectedCategoryDropown", selectedCategoryDropown)
    return (
      <CardItem cloth={cloth} />
    )
  });

  if(loading) {
    return <h2>Loading...</h2>
  }

  // return  (
  //   <>
  //   <StyledCardList>{renderedList}</StyledCardList>
  //   </>
  // )
  if(cat === "All Items") {
    return  (
      <>
      <StyledCardList>{renderedList} Page Number: {activeLink}</StyledCardList>
      </>
    )
  } else {
    return (
      <StyledCardList>{renderedCategoryList} Page Number: {activeLink}</StyledCardList>
    )
  }

}

export default CardList;
